.checkbox {
  width: 250px;
}

.checkbox__span {
  position: relative;
  font-size: 19px;
  line-height: 18px;
  padding-left: 90px;
  padding-top: 10px;
  display: block;
  width: 200px;

  &::before {
    content: "";
    position: absolute;
    width: 38px;
    height: 38px;
    left: 0;
    top: -8px;
    background: transparent;
    border: 2px solid $based-gray-2;

    @media (min-width: $tablet-width) {
      width: 52px;
      height: 52px;
    }
  }
}

.checkbox__span:hover {
  &::before {
    border: 2px solid #62d1ba;
  }
}

.checkbox__span:active {
  &::before {
    border: 2px solid rgba(0, 0, 0, 0.3);
  }
}

.checkbox__input:checked + .checkbox__span::after {
  content: "";
  position: absolute;
  width: 26px;
  height: 13px;
  left: 6px;
  top: 0;
  background: transparent;
  border-bottom: 2px solid black;
  border-left: 2px solid black;
  transform: rotate(-45deg);

  @media (min-width: $tablet-width) {
    width: 32px;
    height: 22px;
    left: 10px;
  }
}
