.radio__label {
  position: relative;
  padding-left: 60px;
  display: block;
}

.radio__span:hover {
  &::before {
    border: 2px solid #62d1ba;
  }
}

.radio__span:active {
  &::before {
    border: 2px solid rgba(0, 0, 0, 0.3);
  }
}

.radio__span {
  font-size: 19px;
  line-height: 18px;

  &::before {
    content: "";
    position: absolute;
    width: 42px;
    height: 42px;
    left: 0;
    top: -8px;
    background: transparent;
    border: 2px solid $based-gray-2;
    border-radius: 50%;
  }
}

.radio__input:checked + .radio__span::after {
  content: "";
  position: absolute;
  width: 18px;
  height: 18px;
  left: 12px;
  top: 4px;
  background: $based-cyan-3;
  border-radius: 50%;
}
